* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 400px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6x #999, 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    height: 80%;
    overflow: hidden;
    text-align: center;
    background: darkgray;
}

.card > .img-container > img {
    width: 60%
}

.card > .content {
    padding-left: .25rem;
    padding-right: .25rem;
    font-size: 15px;
}

.card > .content > ul {
    list-style-type: none;
}

.card > .content > ul > li {
    margin-top: 18px;
}

.wrapper {
    padding-top: 50px;
    background: forestgreen;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}

.header {
    width: 100%;
    font-size: 2rem;
    color: black;
    margin-bottom: 10px;
}

.title {
    float: left;
}

.score {
    float: right;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
